body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  margin: 2rem auto;
  text-align: center;
  max-width: 1500px;
}

body {
  background-color: rgb(26, 26, 27);
  font-family: Inter, sans-serif;
  margin: 2rem 1.5rem;
}

.white {
  color: white;
}

.orange {
  color: orange;
}

.des {
  text-align: left;
  margin: 2rem 0px;
  font-size: 0.8rem;
}

.cardBase {
  background-color: rgb(35, 37, 44);
  padding: 2.5rem 3rem;
  max-width: 700px;
  min-width: 400px;
  display: flex;
  justify-content: center;
  margin: 1rem 1.5rem;
  line-height: normal;
}

.row {
  margin: auto;
}

.cardContainer {
  background-color: rgb(30, 33, 41);
  border-radius: 10px;
  width: 350px;
  overflow: hidden;
}

.btn-row{
  padding: 0.5rem 1.1rem;
}
.btn-row button {
  width: 100%;
  border-radius: 6px;
  border: none;
  padding: 9px 0px;
}

.profile-image-div{
  height: 340px;
  background-image: url(/business-card/static/media/main.01e76464.jpeg);
  background-repeat: no-repeat;
  background-position-x: center;
  background-size: cover;
}

.white h3,
.des h5 {
  font-weight: bold;
  margin-top: 1.5rem;
}

.des h5,
.des p {
  margin: 0.5rem;
}

#web{
  font-size: 0.8rem;
  margin-top: -5px;
}

.profile-bottom {
  width: 100%;
  background-color: rgb(26, 26, 27);
  padding: 1rem;
}

.profile-bottom .fab {
  padding: 5px;
  font-size: 30px;
  margin: 0px 0.8rem;
  color: #c6d2eb;
}

button .fab,
button .fa {
  font-size: 16px;
  text-decoration: none;
  margin: 0px 7px;
}

.fab:hover {
  opacity: 0.7;
}

.bg-white {
  background-color: white;
}

.bg-blue {
  background-color: rgb(80, 147, 226);
  color: white;
}

a {
  text-decoration: none;
  color: inherit;
}
